import Link from "next/link";
import styled from "styled-components";

const HeaderBox = styled.div`
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 0;
`;

const LinkStyle = styled.a`
  flex: 1;
  width: 10em;
  cursor: pointer;
  text-align: center;
  border-width: 0 1px;
  border-style: solid;
  border-color: palevioletred;
  :hover {
    color: palevioletred;
  }
`;

const HEADER_LINKS = [
  { path: "/", name: "Home" },
  { path: "/about", name: "About" },
  { path: "/albums", name: "Albums" }
];

export default function Header() {
  return (
    <HeaderBox>
      {HEADER_LINKS.map((link, index) => (
        <Link href={link.path}>
          <LinkStyle index={index}>{link.name}</LinkStyle>
        </Link>
      ))}
    </HeaderBox>
  );
}
